import { apiGetBanner, apiGetRecommendSongs, apiGetIndexData, apiGetTopAlbum, apiGetRelatedVideo, apiGetHotList } from '../../apis/index.js';
import songList from '../../components/songList.vue';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import MescrollUni from '@/components/mescroll-uni/mescroll-uni.vue';
import search from '@/components/search.vue';

export default {
	components: {
		songList,
		search,
		uniNavBar,
		MescrollUni
	},
	data() {
		return {
			searchTxt: '周杰伦',
			isShowSearch: false,
			// 下拉刷新的常用配置
			downOption: {
				auto: false
			},
			// 上拉加载的常用配置
			upOption: {
				auto: false,
				page: {
					num: 0,
					size: 10
				}
			},
			swiper: [],
			curDate: '',
			contentBar: [{ name: '每日推荐' }, { name: '歌单' }, { name: '排行榜' }, { name: '电台' }, { name: '直播' }],
			recommendSongs: [],
			newType: 1,
			latestTempAlbum: [],
			latestAlbum: [],
			relatedVideo: [],
			hotList: []
		};
	},
	onLoad() {
		this.getBanner();
		this.getRecommendList();
		this.getLatestAlbum();
		this.getRelatedVideo();
		this.getHotList();

		this.curDate = new Date().getDate();
	},
	methods: {
		// 获取轮播图
		getBanner() {
			apiGetBanner().then(res => {
				this.swiper = res.banners;
			});
		},
		// 获取推荐歌单列表
		getRecommendList() {
			const params = {
				limit: 6
			};
			apiGetRecommendSongs(params).then(res => {
				// 格式化播放量数据
				const formatCount = data => {
					let tempNum = data;
					if (data > 100000) {
						tempNum = parseInt(data / 10000) + '万';
					}
					return tempNum;
				};

				this.recommendSongs = res.result;

				this.recommendSongs.forEach(item => {
					item.playCount = formatCount(item.playCount);
				});
			});
		},
		// 切换新碟新歌
		switchTab(type) {
			this.newType = type;
			let temp = {
				s: type == 1 ? 0 : 3,
				e: type == 1 ? 3 : 6
			};
			this.latestAlbum = this.latestTempAlbum.slice(temp.s, temp.e);
		},
		// 获取新碟
		getLatestAlbum() {
			apiGetTopAlbum().then(res => {
				this.latestTempAlbum = res.albums;
				this.latestAlbum = res.albums.slice(0, 3);
			});
		},
		// 获取相关视频
		getRelatedVideo() {
			const params = {
				id: 6524
			};
			apiGetRelatedVideo(params).then(res => {
				this.relatedVideo = res.data;
			});
		},
		getList(pageNum, pageSize, successCallback, errorCallback) {
			const params = {
				id: 4571 + pageNum,
				pageNum,
				pageSize
			}
			apiGetRelatedVideo(params).then(res => {
				successCallback && successCallback(res.data)
			}, res => {
				errorCallback && errorCallback()
			})
		},
		// hot 列表
		getHotList() {
			const params = {
				limit: 10,
				order: 'hot'
			}
			apiGetHotList(params).then(res => {
				this.hotList = res.playlist;
			})
		},
		// 跳转链接
		goUrl(url) {
			uni.navigateTo({
				url: url
			})
		},
		// 打开搜索
		openSearch() {
			this.isShowSearch = true
			this.$refs.search.open()
		},
		// 关闭搜索
		closeSearch() {
			this.isShowSearch = false;
		},
		// 下拉刷新的回调
		downCallback(mescroll) {
			mescroll.resetUpScroll()
		},
		// 上拉加载的回调
		upCallback(mescroll) {
			this.getList(mescroll.num, mescroll.size, res => {
				// 设置列表
				if (mescroll.num == 1) this.relatedVideo = []
				this.relatedVideo = this.relatedVideo.concat(res)
				mescroll.endSuccess()
			}, () => {
				mescroll.endErr()
			})
		},
		goCloud() {
			uni.showToast({
				icon: 'none',
				title: '功能未开发'
			})
		}
	}
};
